<template>
    <section>
        <tg-list v-show="IsWorkTypeView" :IsShowAction="true" ModuleName="Ticketing" SubModuleCode="FSM_work_type"
            :LegendArray="[]" :RenderRowActionMethod="renderActions" v-on:ActionButtonClick="actionButtonClick"
            :widgets="[]" IdentityColumn="worktype_id" :HeaderText="$t('WorkType')" :listType="['List']"
            :ListData="WorkTypeListing" :HeaderData="Headers" :HeaderButtons="listheaderbuttons"
            :TotalRecords="TotalRecords" :SearchFields="leftSearchFields" :ListDataCallBackFunction="FetchRecords"
            :showCheckBox="true" :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')"
            v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
            v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
            <template v-slot:slotdata="{ data }">
                <template v-if="data.column.COLUMN_NAME == 'worktype_name'">                    
                    <router-link :title="data.row[data.column.COLUMN_NAME]" :to ="'/Ticketing/WorktypeDetail/' + data.row.worktype_id"
                        data-toggle-tooltip="tooltip" >
                        {{ data.row[data.column.COLUMN_NAME] }}</router-link>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'duration_type'">
                    <td v-if="data.row.duration_type == 'HH'"> {{ $t('Hours') }}</td>
                    <td v-if="data.row.duration_type == 'MM'"> {{ $t('Minutes') }}</td>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'SkillName'">
                    <td v-if="data.row.SkillName == null"> -- </td>
                    <td v-if="data.row.SkillName != null"> {{data.row.SkillName}} </td>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'ProductName'">
                    <td v-if="data.row.ProductName == null"> -- </td>
                    <td v-if="data.row.ProductName != null"> {{data.row.ProductName}}</td>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'color'">
                    <td class="text-center single-action tourguide_a">
                        <span :style="{ 'background-color': data.row.color }"
                            style="display: inline-block; width: 60px; border-radius: 10px; height: 17px;">&nbsp;</span>
                    </td>
                </template>
            </template>
        </tg-list>
        <AddWorkTypePopup v-if="AddWorkTypePopup" :WorkTypeId="WtId"></AddWorkTypePopup>
    </section>
</template>

<script>
import Schema from "@/views/WorkType/worktypeSchema";
import DataServices from "@/services/DataService";
import AddWorkTypePopup from '../../components/Modal/AddWorkTypePopup.vue'
export default {
    components: { AddWorkTypePopup },
    data() {
        return {
            showSetupListingSource: false,
            AddWorkTypePopup: false,
            WtId: 0,
            showExpanded: '',
            StatusMappingList: [],
            noRecords: false,
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            listActions: Schema.listActions,
            Headers: [{
                "COLUMN_NAME": "worktype_name",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Work Type",
                "DISPLAY_ORDER": 1
            },
            {
                "COLUMN_NAME": "SkillName",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Skills",
                "DISPLAY_ORDER": 2,
            },
            {
                "COLUMN_NAME": "ProductName",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Product",
                "DISPLAY_ORDER ": 3,
            },
            {

                "COLUMN_NAME": "duration_type",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Duration Type",
                "DISPLAY_ORDER": 4,

            },
            {
                "COLUMN_NAME": "color",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Color",
                "DISPLAY_ORDER": 5,
            }
            ],
            TotalRecords: 0,
            PageNumber: 1,
            PageSize: 10,
            SortBy: '',
            SortExp: '',
            CurrentPage: 1,
            TotalPages: 0,
            SearchCondition: '',
            Color: '',
            WorkTypeListing: [],
            GroupStart: 1,
            GroupEnd: 3,
            conditionForInstantSearch: "",
            user_type: null,
            user_id: null,
            companyId: null,
            IsWorkTypeView: true
        }
    },
    created: async function () {
        this.user_id = this.GetUserInfo.ID
        this.user_type = this.GetUserInfo.UserType
        this.companyId = this.GetUserInfo.CompanyId;
        await this.FetchRecords();
    },
    methods: {
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchRecords();
        },
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchCondition;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchRecords();
        },
        actionButtonClick(callbackfunction, item, index, event) {
            switch (callbackfunction) {
                case 'Edit':
                    this.OpenManagePopup(item.worktype_id)
                    break;
                case 'ClickOnView':
                    this.WorkTypeDeatail(item.worktype_id);
                    break;
            }
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Edit':
                        action.isVisible = true;
                        break;
                    case 'ClickOnView':
                        action.isVisible = true;
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "commonDeleteData":
                    this.DeleteWorkType();
                    break;
                case "openaddWorkType":
                    this.OpenManagePopup(null);
                    break;

            }
        },
        isBlank: function (str) {
            return (!str || /^\s*$/.test(str));
        },
        OpenManagePopup: function (id) {
            var vm = this;
            vm.WtId = id;
            vm.AddWorkTypePopup = !vm.AddWorkTypePopup;
        },
        formatDateTime: function (value) {
            if (value) {
                return moment(value).format(String(Globalize.culture().calendar.patterns.d).toUpperCase() + " " + String(Globalize.culture().calendar.patterns.t).replace("t", "A").replace("t", ""))
            }
        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.GroupStart = this.PageNumber;
            this.GroupEnd = this.PageNumber + 2;
            this.FetchRecords();
        },
        onEnter: function (e) {
            var vm = this;
            if (e.charCode == 13) {
                e.preventDefault();
                vm.SearchCondition = vm.SearchCondition;
                vm.CurrentPage = 1;
                vm.PageSize = 10;
                vm.PageNumber = 1;
                vm.FetchRecords();
            }
        },
        Searchdata: function () {
            var vm = this;
            vm.SearchCondition = vm.SearchCondition;
            vm.FetchRecords();
        },
        refreshList: function () {
            this.SearchCondition = "";
            this.FetchRecords();
        },
        DeleteWorkType: function (event) {
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("WorkType")), function () {
                    var postJSON = {
                        ids: val,
                        companyId: vm.companyId,
                        userId: vm.user_id,
                        moduleName: "FSM",
                        refCode: "TALYGEN_FSM_WORKTYPE"
                    };
                    var objectPostString = JSON.stringify(postJSON);
                    DataServices.deleteActivityData(objectPostString).then(response => {
                        var box = "";
                        if (response.data[0].Status == "Success") {
                            $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                            $("[id^='DeleteMultiple']").removeClass('enable').addClass('disabled');
                            $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                            vm.FetchRecords();
                            box = vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('WorkType')]), "Success", true, vm.$t('Alert'));
                        }
                        else {
                            box = vm.ShowAlert(vm.$t('DeletedError', [vm.$t('WorkType')]), "failure", true, vm.$t('Alert'));
                        }
                        setTimeout(function () {
                            box.modal('hide');
                        }, 2000);

                    });
                });
            }
            else {
                alert(vm.$t("Selectanyrecordtodelete"));
            }
        },
        FetchRecords: async function () {
            var vueObj = this;
            var srchCondition = "";
            var url = `sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&pagesize=${vueObj.PageSize}&pagenumber=${vueObj.PageNumber}`;
            var srchCondition = "";
            if (!this.isBlank(this.searchCondition)) {

                srchCondition = srchCondition + this.searchCondition;
            }
            if (!this.isBlank(this.conditionForInstantSearch)) {
                srchCondition = srchCondition + ((!this.isBlank(this.searchCondition)) ? (" AND " + this.conditionForInstantSearch) : this.conditionForInstantSearch);
            }
            if (srchCondition != "") {
                url = url + "&search=" + srchCondition;
            }
            await DataServices.ServiceAppointment(url)
                .then(function (response) {
                    if (response.data != "" && response.data != null) {
                        if (response.data.result.data.length > 0) {
                            vueObj.Headers.forEach((column) => {
                                column.settings = null;
                                if (column.COLUMN_NAME == 'worktype_name') {
                                    column.settings = {
                                        isInSlot: true
                                    }
                                }
                                if (column.COLUMN_NAME == 'View') {
                                    column.settings = {
                                        isInSlot: true
                                    }
                                }
                                if (column.COLUMN_NAME == 'Edit') {
                                    column.settings = {
                                        isInSlot: true
                                    }
                                }
                                if (column.COLUMN_NAME == 'color') {
                                    column.settings = {
                                        isInSlot: true
                                    }
                                }
                                if (column.COLUMN_NAME == 'duration_type') {
                                    column.settings = {
                                        isInSlot: true
                                    }
                                }
                                if (column.COLUMN_NAME == 'SkillName') {
                                    column.settings = {
                                        isInSlot: true
                                    }
                                }
                                if (column.COLUMN_NAME == 'ProductName') {
                                    column.settings = {
                                        isInSlot: true
                                    }
                                }
                            });
                            vueObj.WorkTypeListing = response.data.result.data;
                            vueObj.TotalRecords = response.data.result.data[0].TOTALRECORDS;
                            vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                            vueObj.CurrentPage = vueObj.PageNumber;
                           $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                            $("[id^='DeleteMultiple']").removeClass('enable').addClass('disabled');
                            $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                            vueObj.noRecord = false;
                        } else {
                            vueObj.noRecord = true;
                            vueObj.WorkTypeListing = [];
                            vueObj.TotalRecords = 0
                        }
                    }
                    else {
                        vueObj.noRecord = true;
                        vueObj.WorkTypeListing = [];
                        vueObj.Headers = [];
                        vueObj.TotalRecords = 0
                    }
                    setTimeout(function () { vueObj.ResponsiveDataTable('tablelistingdata') }, 500);
                })
        },
        WorkTypeDeatail(ths) {
            this.$router.push({ name: "WorkTypeDetail", params: { id: ths } });
        }
    }
}
</script>

